import { array, boolean, mixed, number, object, string } from "yup";

export const FINITE_DIFFERENCE = "FD";
export const FRONTIER_MOLECULAR_ORBITAL = "FMO";
export const LOCAL_LINEAR = "LL";
export const LOCAL_QUADRATIC = "LQ";
export const GLOBAL_LINEAR = "GL";
export const GLOBAL_QUADRATIC = "GQ";
export const LL_OPTIONS = [
  { value: LOCAL_LINEAR + "ff+", label: "Fukui Function +" },
  { value: LOCAL_LINEAR + "ff-", label: "Fukui Function -" },
  { value: LOCAL_LINEAR + "ff0", label: "Fukui Function 0" },
  { value: LOCAL_LINEAR + "ls", label: "Local Softness (Linear)" },
  { value: LOCAL_LINEAR + "p", label: "Density (Linear)" }
];

export const LQ_OPTIONS = [
  { value: LOCAL_QUADRATIC + "dd", label: "Dual Descriptor" },
  { value: LOCAL_QUADRATIC + "f", label: "Fukui Function" },
  { value: LOCAL_QUADRATIC + "hs", label: "Hyper Softness" },
  { value: LOCAL_QUADRATIC + "ls", label: "Local Softness (Quadratic)" },
  { value: LOCAL_QUADRATIC + "p", label: "Density (Quadratic)" }
];

export const GL_OPTIONS = [
  { value: GLOBAL_LINEAR + "ea", label: "Electron Affinity (Linear)" },
  { value: GLOBAL_LINEAR + "ef", label: "Electrofugality (Linear)" },
  { value: GLOBAL_LINEAR + "en", label: "Electronegativity (Linear)" },
  { value: GLOBAL_LINEAR + "ep", label: "Electrophilicity (Linear)" },
  { value: GLOBAL_LINEAR + "eta", label: "Chemical Hardness (Linear)" },
  { value: GLOBAL_LINEAR + "ip", label: "Ionization Potential (Linear)" },
  { value: GLOBAL_LINEAR + "mu+", label: "Chemical Potential +" },
  { value: GLOBAL_LINEAR + "mu-", label: "Chemical Potential -" },
  { value: GLOBAL_LINEAR + "mu0", label: "Chemical Potential 0" },
  { value: GLOBAL_LINEAR + "nf", label: "Nucleofugality (Linear)" },
  { value: GLOBAL_LINEAR + "nm", label: "N Max (Linear)" },
  { value: GLOBAL_LINEAR + "s", label: "Softness (Linear)" }
];

export const GQ_OPTIONS = [
  { value: GLOBAL_QUADRATIC + "ea", label: "Electron Affinity (Quadratic)" },
  { value: GLOBAL_QUADRATIC + "ef", label: "Electrofugality (Quadratic)" },
  { value: GLOBAL_QUADRATIC + "en", label: "Electronegativity (Quadratic)" },
  { value: GLOBAL_QUADRATIC + "ep", label: "Electrophilicity (Quadratic)" },
  { value: GLOBAL_QUADRATIC + "eta", label: "Chemical Hardness (Quadratic)" },
  { value: GLOBAL_QUADRATIC + "ip", label: "Ionization Potential (Quadratic)" },
  { value: GLOBAL_QUADRATIC + "mu", label: "Chemical Potential" },
  { value: GLOBAL_QUADRATIC + "nf", label: "Nucleofugality (Quadratic)" },
  { value: GLOBAL_QUADRATIC + "nm", label: "N Max (Quadratic)" },
  { value: GLOBAL_QUADRATIC + "s", label: "Softness (Quadratic)" }
];

export const DFT_OPTIONS = [
  {
    label: "Local Linear",
    name: LOCAL_LINEAR + "options",
    options: LL_OPTIONS
  },
  {
    label: "Local Quadratic",
    name: LOCAL_QUADRATIC + "options",
    options: LQ_OPTIONS
  },
  {
    label: "Global Linear",
    name: GLOBAL_LINEAR + "options",
    options: GL_OPTIONS
  },
  {
    label: "Global Quadratic",
    name: GLOBAL_QUADRATIC + "options",
    options: GQ_OPTIONS
  }
];
export const CONCEPTUAL_DFT = "CD";

export const CALC_TYPES = [
  { value: FINITE_DIFFERENCE, label: "Finite Difference" },
  { value: FRONTIER_MOLECULAR_ORBITAL, label: "Frontier Molecular Orbital" }
];

export const DFT_SCHEMA = object().shape({
  areFilesReused: boolean().required(),
  calcType: string()
    .required("Please select a type of calculation.")
    .oneOf(CALC_TYPES.map(option => option.value)),
  dftOptions: array()
    .of(
      string().oneOf(
        LL_OPTIONS.map(option => option.value)
          .concat(LQ_OPTIONS.map(option => option.value))
          .concat(GL_OPTIONS.map(option => option.value))
          .concat(GQ_OPTIONS.map(option => option.value))
      )
    )
    .required("Please select at least one option."),
  isCube: boolean().required(),
  isEmailRequested: boolean().required(),
  jobType: string()
    .required("Please select a type of job to submit.")
    .oneOf([CONCEPTUAL_DFT]),
  name: string().required("Please name this job.")
});

export const FMO_SCHEMA = object().shape({
  areFilesReused: boolean().required(),
  calcType: string()
    .required()
    .oneOf(CALC_TYPES.map(option => option.value)),
  file: mixed().required("Please upload a file."),
  jobPk: number()
    .required()
    .positive()
    .integer()
});

export const REUSE_FMO_SCHEMA = object().shape({
  areFilesReused: boolean().required(),
  calcType: string()
    .required()
    .oneOf(CALC_TYPES.map(option => option.value)),
  jobPk: number()
    .required()
    .positive()
    .integer(),
  reusedFile: number()
    .required("Please select a file.")
    .positive()
    .integer()
});

export const FD_SCHEMA = object().shape({
  areFilesReused: boolean().required(),
  calcType: string()
    .required()
    .oneOf(CALC_TYPES.map(option => option.value)),
  file1: mixed().required("Please upload a file."),
  file2: mixed().required("Please upload a file."),
  file3: mixed().required("Please upload a file."),
  jobPk: number()
    .required()
    .positive()
    .integer()
});

export const REUSE_FD_SCHEMA = object().shape({
  areFilesReused: boolean().required(),
  calcType: string()
    .required()
    .oneOf(CALC_TYPES.map(option => option.value)),
  jobPk: number()
    .required()
    .positive()
    .integer(),
  reusedFile1: number()
    .required("Please select a file.")
    .positive()
    .integer(),
  reusedFile2: number()
    .required("Please select a file.")
    .positive()
    .integer(),
  reusedFile3: number()
    .required("Please select a file.")
    .positive()
    .integer()
});



