import Step from "@material-ui/core/Step";
import StepLabel from "@material-ui/core/StepLabel";
import Stepper from "@material-ui/core/Stepper";
import { Theme } from "@material-ui/core/styles/createMuiTheme";
import createStyles from "@material-ui/core/styles/createStyles";
import { WithStyles } from "@material-ui/core/styles/withStyles";
import { Formik } from "formik";
import React from "react";
import Loadable from "react-loadable";
import { ObjectSchema } from "yup";
import DftFilesForm from "../components/DftFilesForm";
import Loading from "../components/Loading";
import { CONCEPTUAL_DFT, DFT_SCHEMA } from "../constants/dftFormData";

const LoadableDftOptionsForm = Loadable({
  loader: () => import("./DftOptionsForm"),
  loading: Loading,
  timeout: 10000 // 10 seconds
});

export const styles = (theme: Theme) =>
  createStyles({
    root: {
      minHeight: "400px"
    }
  });

export interface IDftFormProps extends WithStyles<typeof styles> {
  email: string;
  isEmailVerified: boolean;
  initializedJob: any;
  onSubmitOptions: any;
  onSubmitFiles: any;
  prevUsedFiles: any;
  schema: ObjectSchema<{}>;
}

const DftFormComponent: React.SFC<IDftFormProps> = ({
  classes,
  email,
  isEmailVerified,
  initializedJob,
  onSubmitOptions,
  onSubmitFiles,
  prevUsedFiles
}) => (
  <div className={classes.root}>
    <Stepper activeStep={!initializedJob ? 0 : 1}>
      <Step completed={!!initializedJob}>
        <StepLabel>Select options</StepLabel>
      </Step>
      <Step completed={false}>
        <StepLabel>Choose files</StepLabel>
      </Step>
    </Stepper>
    {!initializedJob ? (
      <Formik
        initialValues={{
          areFilesReused: false,
          isCube: true,
          isEmailRequested: false,
          jobType: CONCEPTUAL_DFT
        }}
        onSubmit={onSubmitOptions}
        validationSchema={DFT_SCHEMA}
        component={LoadableDftOptionsForm}
      />
    ) : (
      <DftFilesForm
        initializedJob={initializedJob}
        prevUsedFiles={prevUsedFiles}
        onSubmitFiles={onSubmitFiles}
      />
    )}
  </div>
);

export default DftFormComponent;



