import withStyles from "@material-ui/core/styles/withStyles";
import { connect } from "react-redux";
import { compose, Dispatch } from "redux";
import { apiActions, dftActions } from "../actions";
import DftFormComponent, { styles } from "../components/DftFormComponent";
import {
  FINITE_DIFFERENCE,
  FRONTIER_MOLECULAR_ORBITAL,
  GLOBAL_LINEAR,
  GLOBAL_QUADRATIC,
  LOCAL_LINEAR,
  LOCAL_QUADRATIC
} from "../constants/dftFormData";
import { IDftOptions, IDftOptionsFormValues, IStoreState } from "../types";

const valuesToOptions = (values: IDftOptionsFormValues) => {
  const options: IDftOptions = {};
  options.name = values.name;
  options.is_email_requested = values.isEmailRequested;
  options.jobtypeoption_set = [
    {
      job_type: values.jobType
    }
  ];
  options.calctypeoption_set = [
    {
      calc: values.calcType
    }
  ];
  const gqOptions = values.dftOptions.filter((option: string) =>
    option.startsWith(GLOBAL_QUADRATIC)
  );
  if (gqOptions) {
    options.globalquadraticoption_set = gqOptions.map((descriptor: string) => ({
      descriptor: descriptor.slice(2)
    }));
  }
  const glOptions = values.dftOptions.filter((option: string) =>
    option.startsWith(GLOBAL_LINEAR)
  );
  if (glOptions) {
    options.globallinearoption_set = glOptions.map((descriptor: string) => ({
      descriptor: descriptor.slice(2)
    }));
  }
  const lqOptions = values.dftOptions.filter((option: string) =>
    option.startsWith(LOCAL_QUADRATIC)
  );
  if (lqOptions) {
    options.localquadraticoption_set = lqOptions.map((descriptor: string) => ({
      descriptor: descriptor.slice(2)
    }));
  }
  const llOptions = values.dftOptions.filter((option: string) =>
    option.startsWith(LOCAL_LINEAR)
  );
  if (llOptions) {
    options.locallinearoption_set = llOptions.map((descriptor: string) => ({
      descriptor: descriptor.slice(2)
    }));
  }
  if (values.isCube) {
    options.cubeoption_set = [{}];
  }
  return options;
};
const onSubmitFiles = (dispatch: Dispatch) => (
  values: any,
  { setSubmitting, setErrors /* setValues and other goodies */ }: any
) => {
  console.log(values); // tslint:disable-line
  if (values.areFilesReused) {
    if (values.calcType === FRONTIER_MOLECULAR_ORBITAL) {
      dispatch(
        dftActions.submitOneReusedFile({
          jobPk: values.jobPk,
          reusedFile: values.reusedFile
        })
      );
    } else if (values.calcType === FINITE_DIFFERENCE) {
      dispatch(
        dftActions.submitThreeReusedFiles({
          jobPk: values.jobPk,
          threeReusedFiles: [
            values.reusedFile1,
            values.reusedFile2,
            values.reusedFile3
          ]
        })
      );
    }
  } else {
    if (values.calcType === FRONTIER_MOLECULAR_ORBITAL) {
      dispatch(
        dftActions.submitOneFile({
          file: values.file,
          jobPk: values.jobPk
        })
      );
    } else if (values.calcType === FINITE_DIFFERENCE) {
      dispatch(
        dftActions.submitThreeFiles({
          jobPk: values.jobPk,
          threeFiles: [values.file1, values.file2, values.file3]
        })
      );
    }
  }
};
const onSubmitOptions = (dispatch: Dispatch) => (
  values: IDftOptionsFormValues,
  { setSubmitting, setErrors /* setValues and other goodies */ }: any
) => {
  if (values.areFilesReused) {
    dispatch(dftActions.getUploadedFiles());
  }
  if (values.isEmailRequested) {
    dispatch(apiActions.queryEmail());
  }
  const options = valuesToOptions(values);
  // console.log(data); // tslint:disable-line
  dispatch(dftActions.submitOptions({ options }));
  /*
    LoginToMyApp(values).then(
      user => {
        setSubmitting(false);
        // do whatevs...
        // props.updateUser(user)
      },
      errors => {
        setSubmitting(false);
        // Maybe transform your API's errors into the same shape as Formik's
        setErrors(transformMyApiErrors(errors));
      }
    );
    */
  // console.log(values); // tslint:disable-line
};

const mapStateToProps = (state: IStoreState) => ({
  email:
    state.profile && state.profile.email
      ? state.profile.email
      : "your email address",
  initializedJob: state.initializedJob,
  isEmailVerified: state.profile && state.profile.email_verified,
  prevUsedFiles: state.prevUsedFiles
});
const mapDispatchToProps = (dispatch: Dispatch) => ({
  onSubmitFiles: onSubmitFiles(dispatch),
  onSubmitOptions: onSubmitOptions(dispatch)
});

const DftForm = compose(
  withStyles(styles),
  connect(
    mapStateToProps,
    mapDispatchToProps
  )
)(DftFormComponent);

export default DftForm;



