import { Formik } from "formik";
import React from "react";
import Loadable from "react-loadable";
import { ObjectSchema } from "yup";
import Loading from "../components/Loading";
import {
  FD_SCHEMA,
  FINITE_DIFFERENCE,
  FMO_SCHEMA,
  FRONTIER_MOLECULAR_ORBITAL,
  REUSE_FD_SCHEMA,
  REUSE_FMO_SCHEMA
} from "../constants/dftFormData";

const LoadableDftFilesFD = Loadable({
  loader: () => import("./DftFilesFD"),
  loading: Loading,
  timeout: 10000 // 10 seconds
});

const LoadableDftFilesFMO = Loadable({
  loader: () => import("./DftFilesFMO"),
  loading: Loading,
  timeout: 10000 // 10 seconds
});

const LoadableDftFilesReuseFMO = Loadable({
  loader: () => import("./DftFilesReuseFMO"),
  loading: Loading,
  timeout: 10000 // 10 seconds
});

const LoadableDftFilesReuseFD = Loadable({
  loader: () => import("./DftFilesReuseFD"),
  loading: Loading,
  timeout: 10000 // 10 seconds
});

export interface IDftFilesFormProps {
  initializedJob: any;
  prevUsedFiles: any;
  onSubmitFiles: any;
}
export const DftFilesForm: React.SFC<IDftFilesFormProps> = ({
  initializedJob,
  prevUsedFiles,
  onSubmitFiles
}: IDftFilesFormProps) => {
  let schema: ObjectSchema<{}> | null = null;
  let component: any | null = null;
  const initialValues = {
    areFilesReused: !!prevUsedFiles,
    calcType: initializedJob.calctypeoption_set[0].calc,
    jobPk: initializedJob.pk
  };
  if (initialValues.calcType === FINITE_DIFFERENCE) {
    if (initialValues.areFilesReused) {
      schema = REUSE_FD_SCHEMA;
      component = LoadableDftFilesReuseFD;
    } else {
      schema = FD_SCHEMA;
      component = LoadableDftFilesFD;
    }
  } else if (initialValues.calcType === FRONTIER_MOLECULAR_ORBITAL) {
    if (initialValues.areFilesReused) {
      schema = REUSE_FMO_SCHEMA;
      component = LoadableDftFilesReuseFMO;
    } else {
      schema = FMO_SCHEMA;
      component = LoadableDftFilesFMO;
    }
  }
  return (
    <Formik
      initialValues={initialValues}
      onSubmit={onSubmitFiles}
      component={component}
      validationSchema={schema}
    />
  );
};

export default DftFilesForm;



